import uniIcon from '../uni-icon/uni-icon.vue'
export default {
	name: 'semp-notice-bar',
	components: {
		uniIcon
	},
	data() {
		return {								
			boxWidth:'398',
			textwidth:'',
			sw:'0',
			timer:null,				
			showNotice:true
		}
	},
	props:{
		strText:{
			type: String,
			default: '新版震撼发部了！',
		},
		//多条信息滚动模式
		arrayText:{
			type: Array,
			default () {
				return [
					'新版震撼发部了！',
					'人气爆红，发布日流量超过十万',
					'36氪热文榜推荐、CSDN公号推荐分享文章'
				]
			}
		},
		//slider 滑动 scrollTop 上下滚动 scrollLeft横向滚动
		showType:{
			type: String,
			default: 'slider',
		},
		showTime: {
			type: [Number, String],
			default: ''
		},
		icon: {
		  type: String,
		  default: 'sound',
		},
		setIconColor: {
		  type: String,
		  default: '#2F86F6',
		},
		bgColor: {
		  type: String,
		  default: 'rgba(89, 158, 248, .08)',
		},
		color: {
		  type: String,
		  default: '#2F86F6',
		},
		 scrollable: {
		  type: [Boolean, String],
		  default: false,
		},
		//圆角
		round: {
		  type: [Boolean, String],
		  default: false,
		},
		//是否多行
		rows: {
		  type: [Boolean, String],
		  default: false,
		},
	},
	 mounted() {			
		if(this.scrollable && !this.rows && this.showType=="slider"){				
			this.sinit();			
			this.move();
		}			
		this.close()			
	},
	methods: {
		sinit(){
			let query=uni.createSelectorQuery();
			let view = query.select(".text-box");
			view.fields({
					size: true,
					scrollOffset: true
				}, data => {						
					this.textwidth=data.width;
			}).exec();
			let box=query.select(".show-box");
			box.fields({
					size: true,
					scrollOffset: true
				}, data => {
					console.log("得到节点信息" + JSON.stringify(data));											
					this.boxWidth=data.width;
			}).exec();
		},
		move(){				
			// this.sw=this.boxWidth;											
			//console.error('运行长度:'+this.textwidth)
			this.timer=setInterval(() =>{
				this.sw=this.sw-1;
				if(-this.sw-this.textwidth>0){
					//clearInterval(this.timer);
					this.sw=this.boxWidth
				}					
			},25)
		},
		show(){
			this.showNotice=true;
		},
		close(){
			if((this.timer || this.showType!='slider') && this.showTime!=''){
				setInterval(()=>{
					if(this.timer){
						clearInterval(this.timer);
					}	
					this.showNotice=false;
				},this.showTime)					
			}	
			this.$emit("close");
		},
		onItemClick(){
			this.$emit('click')
		}
	},		
}
